import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/components/HomePage.vue'
import UploadTemplate from '@/components/UploadTemplate.vue'
import DefineTemplate from '@/components/DefineTemplate.vue'
import TemplateList from '@/components/TemplateList.vue'
import MakePlan from '@/components/MakePlan.vue'
import UserPlanList from '@/components/UserPlanList.vue'
import UserDocList from '@/components/UserDocList.vue'

const routes = [
  // 你可以在这里继续添加更多的子页面路由
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/uploadTemplate',
    name: 'UploadTemplate',
    component: UploadTemplate
  },
  {
    path: '/defineTemplate',
    name: 'DefineTemplate',
    component: DefineTemplate
  },
  {
    path: '/templateList',
    name: 'TemplateList',
    component: TemplateList
  },
  {
    path: '/makePlan',
    name: 'MakePlan',
    component: MakePlan
  },
  {
    path: '/userPlanList',
    name: 'userPlanList',
    component: UserPlanList
  },
  {
    path: '/userDocList',
    name: 'userDocList',
    component: UserDocList
  },
]

const router = createRouter({
  // mode: 'hash', 
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
