<template>
    <el-row>
        <el-col :span="12">
          <el-button @click="makeWordPlan" type="primary">生成Word文档</el-button>
        </el-col>

        <el-col :span="12">
          <el-button @click="makeBatchPlanCTT" type="warning">BATCH生成预案</el-button>
        </el-col>
      </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column prop="id" label="ID" width="180"></el-table-column>
      <el-table-column prop="label" label="预案名称" width="180"></el-table-column>
      <el-table-column prop="order" label="序号" width="180"> </el-table-column>
      <el-table-column label="内容操作" width="280">
        <template #default="scope">
            <!-- 在这里定义自定义内容 -->
            <el-button @click="deletePlanNode(scope.row)">删除</el-button>
            <el-button @click="doLLMGen(scope.row)" v-if="scope.row.result">重新生成</el-button>
            <el-button @click="doLLMGen(scope.row)" v-if="!scope.row.result">生成</el-button>
        </template>
      </el-table-column>
      <el-table-column label="生成结果">
        <template #default="scope">
            <!-- 在这里定义自定义内容 -->
            <div v-html="scope.row.result"></div>
            <!-- {{ scope.row.result }} -->
        </template>
      </el-table-column>
    </el-table>
  </template>
  <script>
  import axios from 'axios';
  import { ElMessage } from 'element-plus';
  
  export default {
    name: 'ListDemo',
    data() {
      return {
        tableData: [
        ]
      }
    },
    methods: {
    makeWordPlan() {  
      console.log('makeWordPlan');
      axios.post('/api/yaapp/makeYuAnWord',{
        id: this.$route.query.id
      } 
      ).then(response => {  
            console.log('Response:', response.data);
            ElMessage.success(response.data.msg);
      }).catch(error => {  
        console.info('Error:', error.response ? error.response.data : '请求失败');  
        // 如果删除失败，可以调用reject()
      });
    },

    makeBatchPlanCTT() {  
      axios.post('/api/yaapp/llmYuANPlan', 
        {"ptid": this.$route.query.id}
      ).then(response => {  
            console.log('Response:', response.data);
            if (response.data.code == 200) {
              this.fetchData();
            }else{
                ElMessage.error(response.data.msg);
            }
      }).catch(error => {  
        console.info('Error:', error.response ? error.response.data : '请求失败');  
        // 如果删除失败，可以调用reject()
      });
    },

    deletePlanNode(row) {
        console.log(row);
        axios.post('/api/yaapp/yuAnUserPtDelete', {
          nodeId: row.id,
          id: this.$route.query.id,
        }).then(response => {
            console.log('Response:', response.data);
            if (response.data.code == 200) {
                ElMessage.success(response.data.msg);
                this.fetchData();
            }else{
                ElMessage.error(response.data.msg);
            }
          }
        ).catch(error => {
            console.info('Error:', error.response ? error.response.data : '请求失败');
            // 如果删除失败，可以调用reject()
        });
      },
    doLLMGen(row) {
        // 处理编辑逻辑
      console.log('大模型生成功能:', row);
      row['ptid'] = this.$route.query.id;
      row['nodeid'] = row.id
      axios.post('/api/yaapp/llmYuANSingleNodePlan', 
        row
      ).then(response => {  
            console.log('Response:', response.data);
            if (response.data.code == 200) {
                row.result = response.data.data.result;
            }else{
                ElMessage.error(response.data.msg);
            }
      }).catch(error => {  
        console.info('Error:', error.response ? error.response.data : '请求失败');  
        // 如果删除失败，可以调用reject()
      });
    },
    handleEdit(row) {
      row.editable = true;
      this.$nextTick(() => {
        // 在下一个tick中聚焦到输入框
        const input = this.$refs[`input-${row.id}`];
        if (input) {
          input.focus();
        }
      });
    },
    handleSave(row) {
      row.editable = false;
      // 这里可以添加保存逻辑，例如发送请求到后端
      console.log('保存', row);
      axios.post('/api/apis/addOrUpdateNode', 
        row
      ).then(response => {  
            console.log('Response:', response.data);
            if (response.data.code == 200) {
                ElMessage.success(response.data.msg);
            }else{
                ElMessage.error(response.data.msg);
            }
      }).catch(error => {  
        console.info('Error:', error.response ? error.response.data : '请求失败');  
        // 如果删除失败，可以调用reject()
      });
    },
    
    fetchData() {  
        axios.get('/api/yaapp/yuAnUserPtDetail', {params: {
            id: this.$route.query.id
        }}).then(response => {  
            console.log('Response:', response.data);
            this.tableData = response.data.data.nodeOutlineList;
        }).catch(error => {  
            console.error('Error fetching data:', error);  
        });  
        },
    },
    mounted() {
        console.log("init");
        this.fetchData();
    },
  }
  </script>
  