<template>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column prop="id" label="ID" width="180"></el-table-column>
      <el-table-column prop="name" label="预案名称" width="180"></el-table-column>
      <el-table-column prop="ctype" label="预案类型"></el-table-column>
      <el-table-column label="操作">
        <template #default="scope">
            <el-button size="mini" @click="handleRowClick(scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
  </template>
  
  <script>
  import axios from 'axios';

  export default {
    name: 'ListDemo',
    data() {
      return {
        tableData: [
        ]
      }
    },
    methods: {
      handleRowClick(row,) {
        // row 是当前行的数据
        // column 是当前列的定义
        // event 是原生的点击事件
        console.log('行被点击了:', row);
        // 在这里执行你需要的操作
        const targetId = row.id;
        this.$router.push({ path: '/makePlan', query: { id: targetId } });
      },
      handleSynPlan(row,) {
        // row 是当前行的数据
        // column 是当前列的定义
        // event 是原生的点击事件
        console.log('同步操作:', row);
        // 在这里执行你需要的操作
        axios.post('/api/apis/use/synplan').then(response => {  
            console.log('Response:', response.data);
        }).catch(error => {
            console.error('Error fetching data:', error);  
        });  
      },
      fetchData() {  
        axios.get('/api/yaapp/templateList').then(response => {  
            console.log('Response:', response.data);
            this.tableData = response.data.data;
        }).catch(error => {  
            console.error('Error fetching data:', error);  
        });  
        },
    },
    mounted() {
        console.log("init");
        this.fetchData();
    },
  }
  </script>
  