<template>
  <div class="upload-container">
    <div class="upload-area" @dragover.prevent @drop="handleDrop">
      <input type="file" ref="fileInput" @change="handleFileSelect" accept=".doc,.docx" multiple />
      <p v-if="!files.length">拖放Word文档到这里或点击选择文件</p>
      <ul v-if="files.length" class="file-list">
        <li v-for="(file, index) in files" :key="file.name">
          <span>{{ file.name }}</span>
          <button @click="removeFile(index)">删除</button>
        </li>
      </ul>
      <div v-if="uploadProgress > 0 && uploadProgress < 100" class="progress-bar">
        <div :style="{ width: `${uploadProgress}%` }"></div>
      </div>
      <p v-if="uploadProgress === 100">上传成功！</p>
      <p v-if="uploadError">{{ uploadError }}</p>
    </div>
    <button @click="clearSelection">清除选择</button>
    <button @click="startUpload">开始上传</button>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import axios from 'axios';

const fileInput = ref(null);
const files = ref([]);
const uploadProgress = ref(0);
const uploadError = ref('');



function handleFileSelect(event) {
  const selectedFiles = event.target.files;
  Array.from(selectedFiles).forEach(file => {
    if (file.type.startsWith('application/vnd.openxmlformats-officedocument.wordprocessingml.document') || file.type.startsWith('application/msword')) {
      files.value.push(file);
    }
  });
}

function handleDrop(event) {
  event.preventDefault();
  const files = event.dataTransfer.files;
  Array.from(files).forEach(file => {
    if (file.type.startsWith('application/vnd.openxmlformats-officedocument.wordprocessingml.document') || file.type.startsWith('application/msword')) {
      files.value.push(file);
    }
  });
}

function removeFile(index) {
  files.value.splice(index, 1);
}

function clearSelection() {
  files.value = [];
  uploadProgress.value = 0;
  uploadError.value = '';
  fileInput.value.value = ''; // 清除文件输入框的值
}

async function startUpload() {
  if (!files.value.length) return;

  uploadProgress.value = 0;
  uploadError.value = '';

  const formData = new FormData();
  files.value.forEach((file, index) => formData.append(`documents[${index}]`, file));


  try {
    const response = await axios.post('/api/apis/upload', formData, {
      headers: { 'Content-Type': 'multipart/form-data' },
      onUploadProgress: (progressEvent) => {
        const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        uploadProgress.value = percentCompleted;
      }
    });

    console.log('Upload response:', response.data);
    // 上传成功后的处理
    uploadProgress.value = 100;
  } catch (error) {
    uploadError.value = '上传失败，请重试。';
    console.error('Error uploading documents:', error);
  }
}

// 初始化组件时聚焦文件输入框
onMounted(() => {
  fileInput.value.focus();
});


</script>

<style scoped>
.upload-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

.upload-area {
  background-color: #f4f4f4;
  border: 2px dashed #aaa;
  border-radius: 5px;
  padding: 20px;
  width: 300px;
  text-align: center;
  cursor: pointer;
}

.upload-area p {
  margin: 10px 0;
}

.file-list {
  list-style-type: none;
  padding: 0;
  margin-top: 10px;
}

.file-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.progress-bar {
  height: 20px;
  background-color: #eee;
  margin: 10px 0;
  position: relative;
}

.progress-bar div {
  height: 100%;
  background-color: #007bff;
  transition: width 0.3s ease-in-out;
}

button {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
</style>