<template>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column prop="id" label="ID" width="180"></el-table-column>
      <el-table-column prop="name" label="预案名称" width="180"></el-table-column>
      <el-table-column prop="file_path" label="预案文件" width="180"></el-table-column>
      <el-table-column label="下载">
        <template #default="scope">
            <el-button size="mini" @click="handleRowClick(scope.row)">查看</el-button>
            <el-button size="mini" @click="deletePlan(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </template>
  <script>
  import axios from 'axios';
  import { ElMessage } from 'element-plus';

  export default {
    name: 'PlanListDemo',
    data() {
      return {
        tableData: [
        ]
      }
    },
    methods: {
      handleRowClick(row,) {
        // row 是当前行的数据
        // column 是当前列的定义
        // event 是原生的点击事件
        console.log('行被点击了:', row);
        // 在这里执行你需要的操作
        const targetId = row.id;
        this.$router.push({ path: '/makePlan', query: { id: targetId } });
      },
      handleDownload(row) {
        console.log(row);
        // 定义文件URL
      const fileUrl = '/api'+ row.url;
      // 创建隐藏的a标签
      const element = document.createElement('a');
      element.href = fileUrl;
      element.download = row.title + '.docx'; // 指定下载文件名
      document.body.appendChild(element);
      // 触发点击事件
      element.click();
      // 移除元素
      document.body.removeChild(element);
      },

      deletePlan(row) {
        console.log(row);
        axios.post('/api/yaapp/yuAnUserPlanDelete', {
          id: row.id
        }).then(response => {
            console.log('Response:', response.data);
            if (response.data.code == 200) {
                ElMessage.success(response.data.msg);
                this.fetchData();
            }else{
                ElMessage.error(response.data.msg);
            }
          }
        ).catch(error => {
            console.info('Error:', error.response ? error.response.data : '请求失败');
            // 如果删除失败，可以调用reject()
        });
      },
      fetchData() {  
        axios.get('/api/yaapp/yuAnUserPlanList',).then(response => {  
            console.log('Response:', response.data);
            this.tableData = response.data.data;
        }).catch(error => {  
            console.error('Error fetching data:', error);  
        });  
        }
      },
    mounted() {
        console.log("init");
        this.fetchData();
    },
  }
  </script>
  