<template>
    <div class="tree-container">
      <el-row>
        <el-col :span="12">
          <el-button @click="addNode" type="primary">添加节点</el-button>
          <el-button @click="removeNode" type="danger">删除节点</el-button>
        </el-col>
        <el-col :span="12" style="text-align: right;">
          <el-input v-model="searchQuery" placeholder="搜索节点" clearable @keyup.enter="searchNodes"></el-input>
        </el-col>
      </el-row>
      <el-tree
        :data="treeData"
        :props="defaultProps"
        node-key="id"
        :expand-on-click-node="false"
        :show-checkbox="false"
        :default-expanded-keys="expandedKeys"
        :default-checked-keys="checkedKeys"
        highlight-current
        ref="treeRef"
        :filter-node-method="filterNode"
        @check-change="handleCheckChange"
      >
        <template #default="{ node, data }">
          <div class="custom-tree-node">
            <el-checkbox :indeterminate="isIndeterminate(data)" :value="isChecked(data)" @change="toggleCheck(data)">
              {{ node.label }}
              <el-tag v-if="editingNodeId === data.id" effect="dark">{{ editingText }}</el-tag>
              <el-input
                v-if="editingNodeId === data.id"
                v-model="editingText"
                size="small"
                @blur="saveEdit(data)"
                @keyup.enter="saveEdit(data)"
              ></el-input>
            </el-checkbox>
            <span class="node-description" v-if="!editingNodeId || editingNodeId !== data.id">
              {{ data.description }}
              <el-tag v-if="editingDescriptionId === data.id" effect="dark">{{ editingDescription }}</el-tag>
              <el-input
                v-if="editingDescriptionId === data.id"
                v-model="editingDescription"
                size="small"
                @blur="saveDescription(data)"
                @keyup.enter="saveDescription(data)"
              ></el-input>
            </span>
          </div>
          <span class="node-actions">
            <el-button type="text" size="small" @click.stop="handleNodeMove(data)">移动</el-button>
            <el-button type="text" size="small" @click.stop="editNode(data)">编辑名称</el-button>
            <el-button type="text" size="small" @click.stop="editDescription(data)">编辑描述</el-button>
            <el-button type="text" size="small" @click.stop="addSubNode(data)">添加子节点</el-button>
          </span>
        </template>
      </el-tree>
    </div>
  </template>
  
  <script setup>
  import { ref, onMounted,watch, nextTick } from 'vue';
  import { ElMessage, ElMessageBox } from 'element-plus';
  import axios from 'axios';

  // 树数据
  const treeData = ref([]);
  const defaultProps = ref({
    children: 'children',
    label: 'label',
  });
  
  const expandedKeys = ref([]);
  const checkedKeys = ref([]);
  const searchQuery = ref('');
  const editingNodeId = ref(null);
  const editingText = ref('');
  const editingParentId = ref(null);
  const editingDescriptionId = ref(null);
  const editingDescription = ref('');
  const treeRef = ref(null);

  const forceUpdateTree = () => {
    treeRef.value?.$forceUpdate();
  };
  watch(() => treeData,
    () => {
        forceUpdateTree();
    },
    { deep: true }
  );
  // 添加节点
  const addNode = () => {
    const newNode = {
      label: '新节点',
      description: '这是新节点的描述',
    };
    
    const checkedNodes = treeRef.value?.getCheckedNodes();
    if (checkedNodes && checkedNodes.length > 0) {
        newNode.pid = checkedNodes[0].id;
    }

    console.info("addNode: ", newNode)
    return new Promise((resolve, reject) => {
        setTimeout(() => {
          axios.post('/api/apis/addOrUpdateNode', 
                newNode
            ).then(response => {  
                console.log('Response:', response.data);
                if (response.data.code == 200) {
                    resolve();
                    newNode['id'] = response.data.data.id;
                    // 将新节点添加到第一个父节点的子节点列表中
                    // treeData.value[0].children.push(newNode);
                    // treeData.value.push(newNode);
                    // parentNode.children.push(newNode);
                    const parentNode = findNodeById(response.data.data.parent);
                    console.info("parentNode: ", parentNode)
                    if (parentNode) {
                        if (!parentNode.children) {
                            parentNode.children = [];
                        }
                        parentNode.children.push(newNode);
                    }else{
                        treeData.value.push(newNode);
                    }
                    ElMessage.success('节点添加成功');
                }else{
                    reject(new Error(response.data.msg));
                }
          }).catch(error => {  
            console.info('Error:', error.response ? error.response.data : '请求失败');  
            // 如果删除失败，可以调用reject()
            reject(new Error('操作失败，服务器错误'));
          });
        }, 500);
    });
  };

  
  // 添加子节点
  const addSubNode = (parentNode) => {
    const newNode = {
      label: '新子节点',
      description: '这是新子节点的描述',
    };

    const checkedNodes = treeRef.value?.getCheckedNodes();
    if (checkedNodes && checkedNodes.length > 0) {
        newNode.pid = checkedNodes[0].id;
    }
    return new Promise((resolve, reject) => {
        setTimeout(() => {
          console.info("addSubNode: ", newNode)
          axios.post('/api/apis/addOrUpdateNode', 
                newNode
            ).then(response => {  
                console.log('Response:', response.data);
                if (response.data.code == 200) {
                    resolve();
                    newNode['id'] = response.data.data.id;
                    // 将新节点添加到第一个父节点的子节点列表中
                    // treeData.value[0].children.push(newNode);
                    // treeData.value.push(newNode);
                    parentNode.children.push(newNode);
                }else{
                    reject(new Error(response.data.msg));
                }
          }).catch(error => {  
            console.info('Error:', error.response ? error.response.data : '请求失败');  
            // 如果删除失败，可以调用reject()
            reject(new Error('操作失败，服务器错误'));
          });
        }, 1000);
    });
    // 将新节点添加到指定父节点的子节点列表中
  };
  
  // 删除节点
  const removeNode = () => {
    ElMessageBox.confirm('确定要删除这个节点吗？')
      .then(() => {
        const checkedNodes = treeRef.value?.getCheckedNodes();

        checkedNodes.forEach(currentNode => {
            if (currentNode) {
                console.info("removeNode: ", currentNode)
                axios.post('/api/apis/deletenode', { id: currentNode.id }).then(response => {  
                    console.log('Response:', response.data);
                    if (response.data.code == 200) {
                        const parent = getParent(currentNode);
                        if (Array.isArray(parent)) {
                            const index = parent.findIndex(item => item.id === currentNode.id);
                            parent.splice(index, 1);
                        }else{
                            const index = parent?.children.findIndex(item => item.id === currentNode.id);
                            console.info("removeNode: ",index, parent)
                            if (index > -1) {
                                parent.children.splice(index, 1);
                                ElMessage.success('删除成功');
                            }
                        }
                    }else{
                        ElMessage.error(response.data.msg);
                    }
                }).catch(error => {  
                    // 如果删除失败，可以调用reject()
                    ElMessage.error(error.response ? error.response.data.message : '请求失败');
                });
            }
        })
      })
      .catch(() => {});
  };
  
  // 获取节点的父节点
  const getParent = (node) => {
    return treeRef.value?.getNode(node)?.parent?.data;
  };
  
  // 编辑节点名称
  const editNode = (data) => {
    console.info("editNode: ", data)
    editingNodeId.value = data.id;
    editingText.value = data.label;
    editingParentId.value = data.parent;
  };
  
  // 保存节点名称
  const saveEdit = (data) => {
    console.info("saveEdit: ", data)
    if (editingText.value.trim() !== '') {
      data.label = editingText.value;
      axios.post('/api/apis/addOrUpdateNode', 
        data
      ).then(response => {  
            console.log('Response:', response.data);
            if (response.data.code == 200) {
                editingNodeId.value = null;
                editingText.value = '';
                ElMessage.success(response.data.msg);
            }else{
                ElMessage.error(response.data.msg);
            }
      }).catch(error => {  
        console.info('Error:', error.response ? error.response.data : '请求失败');  
        // 如果删除失败，可以调用reject()
      });
    } else {
      ElMessage.error('节点名称不能为空');
    }
  };
  
  // 编辑节点描述
  const editDescription = (data) => {
    editingDescriptionId.value = data.id;
    editingDescription.value = data.description;
    editingParentId.value = data.parent;
  };
  
  // 保存节点描述
  const saveDescription = (data) => {
    if (editingDescription.value.trim() !== '') {
      data.description = editingDescription.value;
      console.info("saveDescription: ", data)
      axios.post('/api/apis/addOrUpdateNode', 
        data
      ).then(response => {  
            console.log('Response:', response.data);
            if (response.data.code == 200) {
                editingDescriptionId.value = null;
                editingDescription.value = '';
                ElMessage.success(response.data.msg);
            }else{
                ElMessage.error(response.data.msg);
            }
      }).catch(error => {  
        console.info('Error:', error.response ? error.response.data : '请求失败');  
        // 如果删除失败，可以调用reject()
      });
      
    } else {
      ElMessage.error('节点描述不能为空');
    }
  };
  
  // 移动节点
  const handleNodeMove = (data) => {
    ElMessageBox.prompt('请输入目标节点的ID', '移动节点', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      inputPattern: /^\d+$/,
      inputErrorMessage: '请输入有效的节点ID',
    }).then(({ value }) => {
      const targetNode = findNodeById(value);
      if (targetNode) {
        moveNode(data, targetNode);
      } else {
        ElMessage.error('未找到指定的节点ID');
      }
    }).catch(() => {
      ElMessage({
        type: 'info',
        message: '取消移动',
      });
    });
  };
  
    // 查找节点
    const findNodeById = (id) => {
    // 遍历树形结构查找指定ID的节点
        const find = (nodes) => {
            for (let i = 0; i < nodes.length; i++) {
                const node = nodes[i];
                if (node.id === id) {
                    return node;
                }
                if (node.children) {
                    const found = find(node.children);
                    if (found) return found;
                }
            }
            return null;
        };
        return find(treeData.value);
    };
  // 移动节点
  const moveNode = (sourceNode, targetNode) => {
    // 从当前父节点中移除节点
    
    const sourceParent = getParent(sourceNode);
    const index = sourceParent?.children.findIndex(item => item.id === sourceNode.id);
    if (index > -1) {
      sourceParent.children.splice(index, 1);
    }
  
    // 将节点插入到目标父节点中
    if (!targetNode.children) {
      targetNode.children = [];
    }
    targetNode.children.push(sourceNode);
  };
  
  // 节点过滤方法
  const filterNode = (value, data) => {
    return data.label.includes(value) || data.description.includes(value);
  };
  
  // 搜索节点
  const searchNodes = () => {
    treeRef.value?.filter(searchQuery.value);
  };
  
  // 处理勾选变化
  const handleCheckChange = (data, checked) => {
    if (checked) {
      checkedKeys.value = [...checkedKeys.value, data.id];
    } else {
      checkedKeys.value = checkedKeys.value.filter(id => id !== data.id);
    }
  };
  
  // 获取节点的半选状态
  const isIndeterminate = (data) => {
    const node = treeRef.value?.getNode(data);
    return node?.indeterminate;
  };
  
  // 获取节点的选中状态
  const isChecked = (data) => {
    const node = treeRef.value?.getNode(data);
    return node?.checked;
  };
  
  // 切换节点的选中状态
  const toggleCheck = (data) => {
    const node = treeRef.value?.getNode(data);
    console.info("toggleCheck: ", node)
    if (node) {
      node.setChecked(!node.checked);
    }
  };
  
   const fetchData =() => {  
      axios.get('/api/apis/templenodes', {  
      }).then(response => {  
        console.log('Data fetched successfully:', response.data);
        treeData.value = response.data.data;  
        treeData.value.forEach(node => expandedKeys.value.push(node.id));
      }).catch(error => {  
        console.error('Error fetching data:', error);  
      });  
    };
  // 初始化
  onMounted(() => {
    nextTick(() => {
      // 初始化展开所有根节点
      fetchData();
    });
  });
  </script>
  <style scoped>
  .tree-container {
    margin-top: 16px;
  }
  
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
  }
  
  .node-description {
    margin-left: 8px;
    color: #666;
  }
  
  .node-actions {
    display: flex;
    gap: 8px;
  }
  
  .el-input {
    width: 150px;
  }
  </style>