<template>
  <el-container>
    <el-header>
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">首页</el-menu-item>
        <el-menu-item index="2">上传模板</el-menu-item>
        <el-menu-item index="3">自定义预案模板</el-menu-item>
        <el-menu-item index="4">方案列表</el-menu-item>
        <el-menu-item index="5">用户预案列表</el-menu-item>
        <el-menu-item index="6">预案文件</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <!-- 子页面内容将在这里显示 -->
      <router-view/>
    </el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: '1'
    }
  },
  methods: {
    handleSelect(key) {
      switch (key) {
        case '1':
          this.$router.push('/');
          break;
        case '2':
          this.$router.push('/uploadTemplate');
          break;
        case '3':
          this.$router.push('/defineTemplate');
          break;
        case '4':
          this.$router.push('/templateList');
          break;
        case '5':
          this.$router.push('/userPlanList');
          break;
        case '6':
          this.$router.push('/userDocList');
          break;
        // 根据需要添加更多的case
      }
    }
  }
}
</script>
